import { Contract } from 'ethers'

import configs from 'configs'

import * as ABI from './abi'

const BUSD = new Contract(configs.tokens.BUSD.address, ABI.ERC20)
const USDT = new Contract(configs.tokens.USDT.address, ABI.ERC20)
const USDC = new Contract(configs.tokens.USDC.address, ABI.ERC20)

const CorePool = new Contract(configs.core.pool.address, ABI.NPool)

const Calimables = {
  BUSD: new Contract(configs.tokens.BUSD.address, ABI.ClaimableERC20),
  USDT: new Contract(configs.tokens.USDT.address, ABI.ClaimableERC20),
  USDC: new Contract(configs.tokens.USDC.address, ABI.ClaimableERC20),
}

export { ABI, BUSD, USDT, USDC, CorePool, Calimables }
