import { useContractFunction, useEthers, useTokenBalance } from '@usedapp/core'
import { constants } from 'ethers'
import type { BigNumber } from 'ethers'
import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux'

import configs from 'configs'
import { Calimables } from 'contracts'

import type { AppDispatch, RootState } from '../context/DApp/store'

// Use throughout your app instead of plain `useDispatch` and `useSelector`
export const useAppDispatch = () => useDispatch<AppDispatch>()
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector
export const useRefreshInfo = () => {}

export const useClaimables = (token: 'BUSD' | 'USDT' | 'USDC') => {
  const { state, send, resetState } = useContractFunction(
    Calimables[token],
    'claim',
    { transactionName: `Claim${token}` }
  )

  const { status, errorCode, errorMessage } = state

  return {
    claim: (amount: BigNumber) => {
      console.log(amount, send)
      send(amount)
    },
    status,
    errorCode,
    errorMessage,
    resetState,
  }
}

const noBalances = Object.fromEntries(
  Object.keys(configs.tokens).map((token) => [token, constants.Zero])
)

export const useBalances = () => {
  const { account } = useEthers()

  const balances = Object.fromEntries(
    Object.entries(configs.tokens).map(([token, { address }]) => {
      // eslint-disable-next-line
      const balance = useTokenBalance(address, account)
      return [token, balance]
    })
  )

  if (!account) return noBalances

  return balances
}
